import { useEffect, useState } from "react";

export default function MoneyRain() {
  const [money, setMoney] = useState([]);
  const [totalAmount, setTotalAmount] = useState(0); // üí∞ ·ÉØ·Éê·Éõ·É£·É†·Éò ·Éó·Éê·Éú·ÉÆ·Éê
  const [playerPosition, setPlayerPosition] = useState(50); // üèÉ‚Äç‚ôÇÔ∏è ·Éõ·Éù·Éó·Éê·Éõ·Éê·É®·Éò·É° X ·Éû·Éù·Éñ·Éò·É™·Éò·Éê
  const [isDancing, setIsDancing] = useState(false); // üï∫ ·Éì·Éù·Éë·Éò ·É™·Éî·Éô·Éï·Éê·Éï·É° ·Éó·É£ ·Éê·É†·Éê?
  const playerSpeed = 8; // üî• ·É°·É¨·É†·Éê·É§·Éò ·Éõ·Éù·É´·É†·Éê·Éù·Éë·Éê!
  const moneyTypes = [
    { src: "/100lari.png", value: 100 },
    { src: "/50lari.png", value: 50 },
    { src: "/20lari.png", value: 100 },
    { src: "/200lari.png", value: 50 },
  ];

  useEffect(() => {
    const createMoney = () => {
      const newMoney = {
        id: Math.random(),
        left: Math.random() * 100 + "vw",
        animationDuration: Math.random() * 4 + 5 + "s",
        ...moneyTypes[Math.floor(Math.random() * moneyTypes.length)],
      };

      setMoney((prev) => [...prev, newMoney]);

      // ‚úÖ ·Éì·Éê·Éî·Éö·Éù·Éì·Éù·É° ·Éì·Éê·É™·Éî·Éõ·Éê·É° ·Éì·Éê ·É®·Éî·Éõ·Éì·Éî·Éí ·É®·Éî·Éê·Éõ·Éù·É¨·Éõ·Éù·É° ·Éõ·Éù·Éó·Éê·Éõ·Éê·É®·Éò·É° ·Éõ·Éì·Éî·Éë·Éê·É†·Éî·Éù·Éë·Éê
      setTimeout(() => {
        setMoney((prev) => prev.filter((item) => item.id !== newMoney.id));
        if (Math.abs(parseFloat(newMoney.left) - playerPosition) < 10) {
          collectMoney(newMoney); // ‚úÖ ·Éõ·Éù·Éó·Éê·Éõ·Éê·É®·Éî·Éõ ·Éê·Éò·É¶·Éù ·É§·É£·Éö·Éò!
        }
      }, (parseFloat(newMoney.animationDuration) * 2000));
    };

    const interval = setInterval(createMoney, 700);
    return () => clearInterval(interval);
  }, [playerPosition]);

  // üéÆ ·Éõ·Éù·Éó·Éê·Éõ·Éê·É®·Éò·É° ·Éõ·Éù·É´·É†·Éê·Éù·Éë·Éê (·Éõ·Éê·É†·É™·ÉÆ·Éú·Éò·Éï ·Éì·Éê ·Éõ·Éê·É†·ÉØ·Éï·Éú·Éò·Éï)
  const handleKeyPress = (event) => {
    if (event.key === "ArrowLeft") {
      setPlayerPosition((prev) => Math.max(prev - playerSpeed, 0)); // ·Éõ·Éê·É†·É™·ÉÆ·Éú·Éò·Éï ·É°·É¨·É†·Éê·É§·Éò
    } else if (event.key === "ArrowRight") {
      setPlayerPosition((prev) => Math.min(prev + playerSpeed, 100)); // ·Éõ·Éê·É†·ÉØ·Éï·Éú·Éò·Éï ·É°·É¨·É†·Éê·É§·Éò
    }
  };

  useEffect(() => {
    window.addEventListener("keydown", handleKeyPress);
    return () => window.removeEventListener("keydown", handleKeyPress);
  }, []);

  // ‚úÖ ·Éõ·Éù·Éó·Éê·Éõ·Éê·É®·Éò·É° ·Éõ·Éò·Éî·É† ·É§·É£·Éö·Éò·É° ·Éê·É¶·Éî·Éë·Éê (·É©·Éê·Éï·Éê·Éõ·Éê·É¢·Éù·Éó ·É°·Éê·É§·É£·Éö·Éî·É®·Éò)
  const collectMoney = (moneyItem) => {
    setTotalAmount((prev) => {
      const newTotal = prev + moneyItem.value;

      // üîÑ ·É†·Éù·É™·Éê 1000‚Çæ-·Éõ·Éì·Éî ·Éê·Éï·Éê, ·Éì·Éù·Éë·Éò ·Éù·É†·ÉØ·Éî·É† ·Éò·É™·Éî·Éô·Éï·Éî·Éë·É°!
      if (newTotal >= 2000) {
        setIsDancing(true);

        // üï∫ 1.5 ·É¨·Éê·Éõ·Éò ·É™·Éî·Éô·Éï·Éê
        setTimeout(() => {
          setIsDancing(false); // ·É®·Éî·É©·Éî·É†·Éî·Éë·Éê
          
          // üï∫ 1 ·É¨·Éê·Éõ·É®·Éò ·Éô·Éï·Éö·Éê·Éï ·É™·Éî·Éô·Éï·Éê
          setTimeout(() => {
            setIsDancing(true);

            // üï∫ 1.5 ·É¨·Éê·Éõ·É®·Éò ·Éò·É°·Éî·Éï ·É®·Éî·É©·Éî·É†·Éì·Éî·Éë·Éê
            setTimeout(() => {
              setIsDancing(false);
              setTotalAmount(0); // üîÑ ·É°·Éê·É§·É£·Éö·Éî ·Éú·É£·Éö·Éì·Éî·Éë·Éê
            }, 1500);
            
          }, 2000);
          
        }, 1500);
      }

      return newTotal;
    });
    playSound(); // üéµ ·É§·É£·Éö·Éò·É° ·Éê·É¶·Éî·Éë·Éò·É° ·ÉÆ·Éõ·Éê
  };

  // üéµ ·ÉÆ·Éõ·Éê ·É§·É£·Éö·Éò·É° ·Éê·É¶·Éî·Éë·Éò·É°·Éê·É°
  const playSound = () => {
    const audio = new Audio("/coin.mp3"); // ·Éì·Éê·Éê·Éõ·Éê·É¢·Éî `public/coin.mp3`
    audio.volume = 0.5;
    audio.play();
  };

  return (
    <div className="fixed top-0 left-0 w-full h-full pointer-events-none z-50">
      {/* üåßÔ∏è ·É§·É£·Éö·Éò·É° ·É¨·Éï·Éò·Éõ·Éê */}
      {money.map((item) => (
        <img
          key={item.id}
          src={item.src}
          alt="Money"
          className="absolute w-16 h-10"
          style={{
            left: item.left,
            animation: `fall ${item.animationDuration} linear forwards`,
          }}
        />
      ))}

      {/* üèÉ‚Äç‚ôÇÔ∏è ·Éõ·Éù·Éó·Éê·Éõ·Éê·É®·Éî ·Éê·Éú ·É™·Éî·Éô·Éï·Éê·Éï·É° */}
      <img
        src="/player.png"
        alt="Player"
        className={`absolute w-30 h-56 transition-all duration-500 ${
          isDancing ? "dance" : ""
        }`}
        style={{
          left: isDancing ? "50%" : `${playerPosition}vw`,
          bottom: isDancing ? "30%" : "-20px",
          transform: isDancing ? "translateX(-50%) scale(1.2)" : "none",
        }}
      />

      {/* üëú ·É°·Éê·É§·É£·Éö·Éî + ·Éó·Éê·Éú·ÉÆ·Éê */}
      <div className="absolute bottom-10 left-10 flex items-center bg-green-800 text-black p-4 rounded-lg shadow-lg">
        <img src="/wallet.png" alt="Wallet" className="w-16 h-14 mr-2" />
        <p className="text-xl font-bold">{totalAmount}‚Çæ</p>
      </div>

      <style jsx>{`
        @keyframes fall {
          from {
            transform: translateY(-10%);
            opacity: 1;
          }
          to {
            transform: translateY(100vh);
            opacity: 0;
          }
        }

        /* üï∫ ·Éì·Éù·Éë·Éò·É° ·É™·Éî·Éô·Éï·Éò·É° ·Éê·Éú·Éò·Éõ·Éê·É™·Éò·Éê */
        @keyframes danceAnimation {
          0% { transform: translateX(-50%) scale(1.2) rotate(0deg); }
          25% { transform: translateX(-50%) scale(1.2) rotate(10deg); }
          50% { transform: translateX(-50%) scale(1.2) rotate(-10deg); }
          75% { transform: translateX(-50%) scale(1.2) rotate(10deg); }
          100% { transform: translateX(-50%) scale(1.2) rotate(0deg); }
        }

        .dance {
          animation: danceAnimation 0.5s infinite alternate ease-in-out;
        }
      `}</style>
    </div>
  );
}
